@import "../../scss/abstracts";
footer {
    width: 100%;
    font-size: 1.5rem;
    text-align: center;
    position: absolute;
    height: 5rem;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    z-index: 100000;
    span {
        color: $color-secondary;
        &:hover {
            @extend .gradient;
        }
    }
}