@import "../../scss/abstracts.scss";
.icons-social {
    text-align: center;
    padding-top: 1rem;
    margin-bottom: 10vh;
}

.icon {
    padding: 1rem 9rem;
    display: inline-block;
    width: 2.9rem;
    height: 2.9rem;
    background-color: $color-primary;
    cursor: pointer;
    transition: all 0.2s ease;
    @include respond(smaller-screen) {
        padding: 1rem 5rem;
        width: 3.2rem;
        height: 3.2rem;
    }
    &:hover {
        animation: 0.5s ease infinite gradient;
    }
    &--linkedin {
        -webkit-mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        mask: url(../../assets/linkedin-in-brands.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
    &--email {
        -webkit-mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        mask: url(../../assets/envelope-solid.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
    &--github {
        -webkit-mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        mask: url(../../assets/github-brands.svg) no-repeat center/contain;
        &:hover {
            @extend .smaller-size;
        }
    }
}

@keyframes gradient {
    from {
        background: linear-gradient(to right, $color-secondary, #2ebf91);
    }
    to {
        background: linear-gradient(to left, $color-secondary, #2ebf91);
    }
}